import Vue from 'vue'
import Vuex from 'vuex'
import groups from '@/store/modules/groups'
import authentication from '@/store/modules/authentication'
import entries from '@/store/modules/entries'
import ads from '@/store/modules/ads'
import news from '@/store/modules/news'
import student_visits from '@/store/modules/student_visits'
import snackbar from '@/store/modules/snackbar'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    url: process.env.NODE_ENV == 'development' ? 'http://127.0.0.1:5020/api/v1' : '/api/v1',
  },

  getters: {
    url(state){
      return state.url
    },
  },

  mutations: {},

  actions: {
    raiseForStatus({commit}, status) {
      if (status == 401) {
        commit('resetLoginStatus', false)
        commit('showSnackbarError', 'Ошибка доступа: вы не авторизированы')
      }
      else if (status == 403) {
        commit('showSnackbarError', 'Ошибка доступа: недостаточно прав')
      }
      else {
        commit('showSnackbarError', 'Неизвестная ошибка сервера')
      }
    },

    fetchJsonData: async ({dispatch, commit, getters, state}, endpoint) => {
      const state_name = endpoint.replaceAll('/','_').replaceAll('-', '_').toUpperCase()
      
      let headers = {}
      if (getters.isLogin) {
        headers['Token'] = getters.token
      }

      const r = await fetch(state.url+endpoint, {headers: headers})
      if (r.status == 200) {
        const json_data = await r.json()
        const data = json_data.data

        commit(state_name, data)
      } 
      else {
        dispatch('raiseForStatus', r.status)
      }
    },

    pushJsonData: async ({commit, getters, state}, kwargs) => {
      /*
       * kwargs prop:
       *   endpoint: req
       *   body: req
       *   method: opt (def: POST)
       *   saveData: opt (def: true)
       */

      const method = kwargs.hasOwnProperty('method') ? kwargs['method'] : 'POST'
      const saveData = kwargs.hasOwnProperty('saveData') ? kwargs['saveData'] : true
      
      const stateName = kwargs['endpoint'].replaceAll('/','_').replaceAll('-', '_').toUpperCase()
      
      let headers = {
        'Content-Type': 'application/json',
      }
      if (getters.isLogin) {
        headers['Token'] = getters.token
      }

      const r = await fetch(state.url+kwargs['endpoint'], {method:method, headers: headers, body: JSON.stringify(kwargs['body'])})
      if (r.status == 200) {
        const jsonData = await r.json()
        const data = jsonData.data
  
        if (saveData) {
          commit(stateName, data)
        }
      }
      else {
        dispatch('raiseForStatus', r.status)
      }
    },
  },

  modules: { groups, authentication, entries, ads, news, student_visits, snackbar }
})
