export default {
	state: {
		avtimeList: [],
		myEntries: [],
		entryIsOpen: false,
	},

	mutations: {
		_ENTRY_CHECK_OPEN(state, payload) {
			state.entryIsOpen = payload
		},

		_ENTRY_GET(state, payload) {
			state.myEntries = payload
		},

		_ENTRY_CREATE(state, payload) {
			state.myEntries = payload
		},

		_AVTIME_GET(state, payload) {
			state.avtimeList = payload
		},
	},

	getters: {
		myEntries(state) {
			return state.myEntries
		},

		entryIsOpen(state) {
			return state.entryIsOpen
		},

		availableTimeList(state) {
			let result = []

			state.avtimeList.forEach((el) => {
				let tmp = result.find(({ weekday }) => weekday == el['weekday'])
				if (tmp == undefined) {
					tmp = result.push({ weekday: el['weekday'], times: [] })
					tmp = result[tmp - 1]
				}

				tmp['times'].push({
					id: el['id'],
					available: el['free_seats'] > 0,
					time: el['time'],
				})
			})

			return result
		},
	},

	actions: {
		fetchAllEntriesOfDay(context, day) {
			const stArr = [
				'Иванов Иван Иванович', 'Иванов Иван Иванович', 'Иванов Иван Иванович',
				'Иванов Иван Иванович', 'Иванов Иван Иванович', 'Иванов Иван Иванович',
				'Иванов Иван Иванович', 'Иванов Иван Иванович', 'Иванов Иван Иванович',
				'Иванов Иван Иванович', 'Иванов Иван Иванович', 'Иванов Иван Иванович',
			]
			return [
				{ time: '16:00', students: stArr },
				{ time: '17:30', students: stArr },
				{ time: '19:00', students: stArr },
				{ time: '20:30', students: stArr },
			]
		}
	}
}