export default {
	state: {
		snackbar: {
			show: false,
			color: '',
			text: '',
			icon: '',
		},
	},

	mutations: {
		showSnackbar(state, payload) {
			state.snackbar.color = payload.color
			state.snackbar.icon = payload.icon
			state.snackbar.text = payload.text
			state.snackbar.show = true
		},
		showSnackbarError(state, payload) {
			state.snackbar.color = '#f44336'
			state.snackbar.icon = 'mdi-alert-octagon'
			state.snackbar.text = payload
			state.snackbar.show = true
		},
		showSnackbarWarning(state, payload) {
			state.snackbar.color = '#fb8c00'
			state.snackbar.icon = 'mdi-alert'
			state.snackbar.text = payload
			state.snackbar.show = true
		},
		showSnackbarSuccess(state, payload) {
			state.snackbar.color = '#4caf50'
			state.snackbar.icon = 'mdi-check-circle'
			state.snackbar.text = payload
			state.snackbar.show = true
		},
		hideSnackbar(state) {
			state.snackbar.show = false
		},
	},

	getters: {
		snackbar(state) {
			return state.snackbar
		},
	},

	actions: {
		showSnackbar({commit}, payload) {
			commit('showSnackbar', payload)
		},
	}
}