export default {
	state: {
		token: '',
		userData: {},
		userStatuses: {
			'COACH': 'Тренер',
			'STUDENT': 'Студент',
			'ADMIN': 'Администратор'
		}
	},

	mutations: {
		_AUTH_SIGNUP(state, payload) {
			localStorage.setItem('Token', payload['token'])
			state.token = payload['token']
			state.userData = payload['user_data']
		},
		
		_AUTH_LOGIN(state, payload) {
			localStorage.setItem('Token', payload['token'])
			state.token = payload['token']
			state.userData = payload['user_data']
		},

		_AUTH_CHECK_LOGIN(state, payload) {
			state.userData = payload
		},

		_AUTH_LOGOUT(state) {
			localStorage.setItem('Token', '')
			state.token = ''
			state.userData = {}
		},
		
		_USER_EDIT(state, payload) {
			Object.assign(state.userData, payload)
		},

		resetLoginStatus(state) {
			localStorage.setItem('Token', '')
			state.token = ''
		},
	},

	getters: {
		isLogin(state) {
			return state.token != ''
		},

		token(state) {
			return state.token
		},

		userData(state) {
			return state.userData
		},

		userStatus(state) {
			return state.userStatuses[state.userData.role]
		},
	},

	actions: {
		loadToken(context) {
			const token = localStorage.getItem('Token')
			context.state.token = token ? token : ''
		},
	}
}