export default {
	state: {
		adsList: [],
	},

	mutations: {
		_ADS_GET(state, payload) {
			state.adsList = payload
		},

		_ADS_CREATE(state, payload) {
			state.adsList = adsList.contact(payload)
		},
	},

	getters: {
		adsList(state) {
			return state.adsList
		},
	},

	actions: {}
}